import React, {useEffect, useState, useLayoutEffect} from 'react';
import {connect} from 'react-redux';
import { Dropdown } from 'semantic-ui-react';

import {fetchProducts} from '../actions';
import RenderCards from './RenderCards';
import '../shop.css';
import Contact from './Contact';

const Shop = (props) =>{ 
    const [currentSort, setSort] = useState("Best Sellers");
    const [currentCategory, setCategory] = useState(props.history.location.state || "All Products");

    useLayoutEffect(() => {
      window.scrollTo(0, 0)
  }, []);

    const sortOptions = [
        {
          key: 'Best Sellers',
          text: 'Best Sellers',
          value: 'Best Sellers',
          icon: "trophy"
        },
        {
            key: 'Price, High To Low',
            text: 'Price, High To Low',
            value: 'Price, High To Low',
            icon: "tags"
          },
        {
          key: 'Price, Low To High',
          text: 'Price, Low To High',
          value: 'Price, Low To High',
          icon: "tag"
        },
      ]

      const categoryOptions = [
        {
          key: 'All Products',
          text: 'All Products',
          value: 'All Products',
          icon: "shopping bag"
        },
        {
            key: 'Buildings',
            text: 'Buildings',
            value: 'Buildings',
            icon: "university"
          },
        {
          key: 'Environment',
          text: 'Environment',
          value: 'Environment',
          icon: "tree"
        },
        {
            key: 'Animals',
            text: 'Animals',
            value: 'Animals',
            icon: "sticker mule"
          },
        {
          key: 'Aesthetic',
          text: 'Aesthetic',
          value: 'Aesthetic',
          icon: "eye"
        },
      ]

    useEffect(() => {
        props.fetchProducts();
    }, []);

    const handleChange1 = (e, {value}) =>{
        setCategory(value);
        props.history.replace(props.location.pathname, value);
    }

    const handleChange2 = (e, {value}) =>{
        setSort(value);
    }

    const filterProducts = () =>{
        if (!props.products || !props.products[0]){return null;}
        if (currentCategory === "All Products"){return sortProducts([...props.products]);}
        let filtered = props.products.filter((item) => item.categories.includes(currentCategory));
        return sortProducts(filtered);
    }

    const sortProducts = (array) =>{
        if (currentSort === "Price, Low To High"){
        return array.sort( function( a , b){
            let item1 = a.sale || a.price;
            let item2 = b.sale || b.price;
            if(parseFloat(item1.slice(1)) > parseFloat(item2.slice(1))) return 1;
            if(parseFloat(item1.slice(1)) < parseFloat(item2.slice(1))) return -1;
            return 0;
        });
        }
        else if (currentSort === "Price, High To Low"){
            return array.sort( function( a , b){
                let item1 = a.sale || a.price;
                let item2 = b.sale || b.price;
                if(parseFloat(item1.slice(1)) < parseFloat(item2.slice(1))) return 1;
                if(parseFloat(item1.slice(1)) > parseFloat(item2.slice(1))) return -1;
                return 0;
            });
            }
        else { return array;}
    }

    return (
    <div style={{display: "flex", flexDirection: "column", textAlign: "center" }}>
        <div className = "shop__bars" style={{display: "flex", justifyContent: "space-between", margin: "25px 5vw 0 5vw"}}>
            <Dropdown className = "shop__categories shop__dropdown"
            onChange={handleChange1}
            placeholder={currentCategory}
            selection
            options={categoryOptions}
            />

            
            <Dropdown className = "shop__dropdown"
            onChange={handleChange2}
            placeholder='Best Sellers'
            selection
            options={sortOptions}
            />



        </div>
        <h1 style={{margin:"0px 25px 35px 25px"}}className = "ui header horizontal divider ">{currentCategory}</h1>
        <RenderCards products = {filterProducts()} background = "shop"/>
        <Contact />
    </div>
    );
};

const mapStateToProps = (state) => {
    return {
        products: Object.values(state.productReducer)
    };
};

export default connect(mapStateToProps, {fetchProducts})(Shop);