import _ from 'lodash'
import React, { Component } from 'react'
import { Search, Grid} from 'semantic-ui-react'
import {connect} from 'react-redux';

import history from '../history';
import {sidebarAction} from '../actions';

const initialState = { isLoading: false, results: [], value: '' }


class SearchProducts extends Component {
    constructor(props){
        super(props);
        this.source= Object.keys(this.props.products).map(key=>{
            return {
                title: this.props.products[key].header,
                description: this.props.products[key].description,
                price: this.props.products[key].sale || this.props.products[key].price,
                image: this.props.products[key].img,
                className: this.props.products[key].sale ? "red" : "",
                onClick: () => {
                    history.push(`/shop/${this.props.products[key].id}`);
                    props.sidebarAction(null);
                }
            }
        })
    }

  state = initialState

  handleResultSelect = (e, { result }) => this.setState({ value: result.title })

  handleSearchChange = (e, { value }) => {
    this.setState({ isLoading: true, value })

    setTimeout(() => {
      if (this.state.value.length < 1) return this.setState(initialState)

      const re = new RegExp(_.escapeRegExp(this.state.value), 'i')
      const isMatch = (result) => re.test(result.title)

      this.setState({
        isLoading: false,
        results: _.filter(this.source, isMatch),
      })
    }, 300)
  }

  render() {
    const { isLoading, value, results } = this.state

    return (
      <Grid>
        <Grid.Column>
          <Search
            fluid
            input={{ fluid: true, icon: 'search', iconPosition: 'left', placeholder: "Search Products"}}
            loading={isLoading}
            onResultSelect={this.handleResultSelect}
            onSearchChange={_.debounce(this.handleSearchChange, 500, {
              leading: true,
            })}
            results={results}
            value={value}
          />
        </Grid.Column>
      </Grid>
    )
  }
}

export default connect(null, {sidebarAction})(SearchProducts);