import React, {useState} from 'react';
import {Link} from 'react-router-dom';

import QuickShop from './QuickShop';
import {CSSTransition} from 'react-transition-group';
import '../reactanimations.css'

const RenderCards = props =>{
    const [currentViewing, setViewing] = useState(null);
    const [currentQuickShop, setQuickshop] = useState(null);

    const renderTrending = () =>{
        if (!props.products || !props.products[0]){return null;}
        return props.products.map((item) =>{return (
          <Link onMouseOver = {() => setViewing(item.id)} onMouseLeave = {() => setViewing(null)} to = {`/shop/${item.id}`} key = {item.id} className = "ui card">
              
              <div className = "image">
                  <img className = "visible content" src={item.img}/>
                  <CSSTransition in={currentViewing === item.id} timeout={500} classNames = "slidedown"> 
                    <div className = {`responsive__quickshopbig ${currentViewing !== item.id ? "quickshop__button" : ""}`} style={{display: "flex", position: "absolute", width: "100%", height: "100%", justifyContent: "center", alignItems: "center", top: "0", left: "0"}}>  
                        <div onClick = {(e)=>{e.preventDefault(); setQuickshop(item.id)}} style={{paddingLeft: "1.5em", paddingRight: "1.5em", marginLeft: "0.75em"}} className = "ui inverted secondary button quickshop__buttondetails">
                            Quick Shop
                        </div>
                    </div>
                  </CSSTransition>
                  <div className = "responsive__quickshopsmall" style={{position: "absolute",  right: "15px", bottom: "15px"}}>
                      <div style = {{color: "white"}} onClick = {(e)=>{e.preventDefault(); setQuickshop(item.id)}}>
                          <i className = "shopping cart big icon"/>
                      </div>
                  </div>
              </div>
  
              <div className = "content">
                  <div className = "header">{item.header}</div>
                  <div style={{display: "flex", justifyContent: "center"}} className = "description">
                     <div style={item.sale ? {textDecoration: "line-through", marginRight: "7px"} : {}}> {item.price}</div>
                      <div style={{color: "red"}}>{item.sale}</div>
                  </div>
              </div>
  
                
          </Link>
        );
        });
      }
    const renderQuickShop = () =>{
        if (currentQuickShop !==null){
            return <QuickShop id = {currentQuickShop} removeQuickShop = {removeQuickShop}/>
        }
    }

    const removeQuickShop = () =>{
        setQuickshop(null);
    }

    return (
        <div className = "ui six doubling centered cards">
            {renderTrending()}
            {renderQuickShop()}
        </div>
    );
};

export default RenderCards;