import React, {useState, useEffect, useLayoutEffect} from 'react';
import {connect} from 'react-redux';
import {fetchProducts} from '../actions';
import {CSSTransition} from 'react-transition-group';


import RenderCards from './RenderCards';
import '../home.css';
import Contact from './Contact';
import history from '../history';
import '../reactanimations.css'


const Home = (props) =>{ 
    const dummyBanners = ["https://i.picsum.photos/id/664/2400/800.jpg?hmac=kw62hBcVVM6jSat_syd7QtaNeTGPDbDq_ZmrtBkeMiY", "https://i.picsum.photos/id/582/2400/800.jpg?hmac=i_DbHbKOBizP1LIWKrN3ZHYQ3CKEOoyCkhHFQDUTOQY", "https://i.picsum.photos/id/259/2400/800.jpg?hmac=bThADbNWcitBs55s1ktWPJEXpLtYq0RJogWSWHiJM0U"];
    const dummyBannerText = [{header: "Environment Collection", subheader: "NEW SEASON"},{header: "Placeholder Banner", subheader: "LOREM"},{header: "Banner Placeholder", subheader: "IPSUM"}]
    useEffect(() => {
        props.fetchProducts();
    }, []);

    useLayoutEffect(() => {
        window.scrollTo(0, 0)
    }, []);

    const [currentBanner, setBanner] = useState(dummyBanners[0]);
    const [showBanner, setShowBanner] = useState(false);

    useEffect(() => {
        setShowBanner(true);
    }, [currentBanner]);

    const renderBanner = () =>{
        return (
        <div style={{position: "relative"}}>
            <CSSTransition in={showBanner} timeout={1000} classNames = "bannerslide" onEnter={() => setShowBanner(false)}> 
                <img className = "ui fluid image" src={currentBanner}></img>
            </CSSTransition>

            <div style={{display: "flex", flexDirection: "row", justifyContent: "center", position: "relative", top: "-25px"}}>
                {renderBannerDots()}   
            </div>
            
            <CSSTransition in={showBanner} timeout={1000} classNames = "bannertextslide"> 
                <div onClick = {currentBanner === dummyBanners[0] ? ()=>history.push("/shop", "Environment") : console.log("dummy banner clicked")} className = {`banner__text ${currentBanner === dummyBanners[0] ? "banner__texthover" : ""}`} style={{position: "absolute", color: "#ffc3b3", width: "100%", height: "100%", display: "flex", justifyContent: "center", alignItems: "center", top: "0", left: "0", flexDirection: "column"}}>
                    <h5 className = "banner__subheader" style={{margin: "0", WebkitTextStroke: "1px black"}}>{currentBanner === dummyBanners[0] ? dummyBannerText[0].subheader : currentBanner === dummyBanners[1] ? dummyBannerText[1].subheader : dummyBannerText[2].subheader}</h5>
                    <h1 className = "banner__header" style={{margin: "0", WebkitTextStroke: "2px black"}}>{currentBanner === dummyBanners[0] ? dummyBannerText[0].header : currentBanner === dummyBanners[1] ? dummyBannerText[1].header : dummyBannerText[2].header}<br/><div style={{WebkitTextStroke: "0px black"}}className = "ui fluid secondary button categories__label banner__button">Explore Now</div></h1>             
                </div>
            </CSSTransition>
        </div>
        );
    }


    const renderBannerDots = () =>{
        return dummyBanners.map((src, index)=> <div onClick = {()=>setBanner(src)} key = {index} className = {currentBanner === src ? "banner__currentdot" : "banner__dot"} />)
    }
    
    const filterTrending = () =>{
        if (!props.products || !props.products[0]){return null;}
        return props.products.filter((item) => item.trending);
    }

    const renderCategories = () =>{
             
        return (
            <div style={{marginBottom: "30px"}}>

                <div className = "responsive__categoriesbig" style={{width: "80%", display: "flex", flexWrap: "wrap", marginLeft: "10%", position: "relative"}}>
                    <div id = "categorybutton__zoomimage__buildings" onClick = {()=>history.push("/shop", "Buildings")} style={{position: "absolute", width: "50%", top: "85%", display: "flex", justifyContent: "center"}}>
                        <div className = "ui secondary button categories__label">Buildings</div>
                    </div>
                    <div id = "sibling__buildings" style={{maxWidth: "50%", overflow: "hidden", marginRight: "2.5%"}}>
                        <img id = "categoryimage__buildings" className = "category__image" onClick = {()=>history.push("/shop", "Buildings")} style={{width: "100%", height: "auto"}} src = "https://i.picsum.photos/id/545/1400/1600.jpg?hmac=09kEIlFTTmSWG_sbilwtBfZHaOkGZ3z0XeUAhkEMRhI"/>
                    </div>

                    
                    <div id = "categorybutton__zoomimage__aesthetic" style={{position: "absolute", width: "25%", display: "flex", top: "32.5%", left: "52.5%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Aesthetic")} className = "ui secondary button categories__label">Aesthetic</div>
                    </div>
                    <div id = "categorybutton__zoomimage__animals" style={{position: "absolute", width: "25%", display: "flex", top: "85%", left: "52.5%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Environment")} className = "ui secondary button categories__label">Environment</div>
                    </div>
                    <div id = "sibling__middle" style={{maxWidth: "25%", display:"flex", flexDirection: "column", marginRight: "2.5%"}}>
                        <div style={{maxWidth: "100%", overflow: "hidden", marginBottom: "3.5%"}}>
                            <img id = "categoryimage__aesthetic" className = "category__image" onClick = {()=>history.push("/shop", "Aesthetic")} style={{ width: "100%", height: "auto"}} src="https://i.picsum.photos/id/249/700/780.jpg?hmac=mlHI87ZFWAOJhPnAfwAdDDltnpLEGsLj5ba_Dir0wbM"/>
                        </div> 
                        <div style={{position: "absolute", top: "48%", zIndex: "5", backgroundColor: "white", width: "25%", height: "1.5%"}}></div>
                        <div style={{maxWidth: "100%", overflow: "hidden"}}>
                            <img id = "categoryimage__animals" className = "category__image" onClick = {()=>history.push("/shop", "Environment")} style={{width: "100%", height: "auto"}} src="https://i.picsum.photos/id/249/700/780.jpg?hmac=mlHI87ZFWAOJhPnAfwAdDDltnpLEGsLj5ba_Dir0wbM"/>
                        </div>
                    </div>
                    
                    <div id = "categorybutton__zoomimage__environment" style={{position: "absolute", width: "20%", display: "flex", left: "80%", top: "85%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Animals")} className = "ui secondary button categories__label">Animals</div>
                    </div>
                    <div  id = "sibling__environment" style={{maxWidth: "20%", overflow: "hidden"}}>
                        <img id = "categoryimage__environment" className = "category__image" onClick = {()=>history.push("/shop", "Animals")} style={{maxWidth: "100%", height: "auto"}}src = "https://i.picsum.photos/id/252/560/1600.jpg?hmac=7YzuCVZltn81FpUEOazVO958v-qlVcinM6wM-VaPpR4"/>
                    </div>
        
                    <div style={{position: "absolute", top: "99%", zIndex: "5", backgroundColor: "white", width: "100%", height: "45px"}}></div>
                </div>

                <div className = "responsive__categoriessmall" style={{width: "80%", display: "flex", flexWrap: "wrap", marginLeft: "10%", position: "relative"}}>
                    <img className = "category__image" onClick = {()=>history.push("/shop", "Buildings")} style={{maxWidth: "100%", height: "auto", marginBottom: "2.5%"}} src = "https://i.picsum.photos/id/545/1400/1600.jpg?hmac=09kEIlFTTmSWG_sbilwtBfZHaOkGZ3z0XeUAhkEMRhI"/>
                    <div style={{maxWidth: "55%", display:"flex", flexDirection: "column", marginRight: "2.5%"}}>
                        <img className = "category__image" onClick = {()=>history.push("/shop", "Aesthetic")} style={{maxWidth: "100%", height: "auto", marginBottom: "2.5%"}} src="https://i.picsum.photos/id/249/700/780.jpg?hmac=mlHI87ZFWAOJhPnAfwAdDDltnpLEGsLj5ba_Dir0wbM"/>
                        <img className = "category__image" onClick = {()=>history.push("/shop", "Environment")} style={{maxWidth: "100%", height: "auto"}} src="https://i.picsum.photos/id/249/700/780.jpg?hmac=mlHI87ZFWAOJhPnAfwAdDDltnpLEGsLj5ba_Dir0wbM"/>
                    </div>
                    <img className = "category__image" onClick = {()=>history.push("/shop", "Animals")} style={{maxWidth: "42.5%", height: "auto"}}src = "https://i.picsum.photos/id/252/560/1600.jpg?hmac=7YzuCVZltn81FpUEOazVO958v-qlVcinM6wM-VaPpR4"/>
                    <div  style={{position: "absolute", width: "100%", display: "flex", top: "40%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Buildings")}  className = "ui secondary button categories__label">Buildings</div>
                    </div>
                    <div style={{position: "absolute", width: "55%", display: "flex", top: "65%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Aesthetic")}  className = "ui secondary button categories__label">Aesthetic</div>
                    </div>
                    <div style={{position: "absolute", width: "55%", display: "flex",  top: "90%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Environment")} className = "ui secondary button categories__label">Environment</div>
                    </div>
                    <div  style={{position: "absolute", width: "42.5%", display: "flex", left: "57.5%", top: "90%", justifyContent: "center"}}>
                        <div onClick = {()=>history.push("/shop", "Animals")} className = "ui secondary button categories__label">Animals</div>
                    </div>
                </div>
            </div>);
    }

    return (
    
    <div style={{display: "flex", justifyContent: "center", flexDirection: "column", textAlign: "center"}}>
        {renderBanner()}
        <h1 style={{margin:"15px 25px 35px 25px"}}className = "ui header horizontal divider ">Categories</h1>
        {renderCategories()}
        <div>
            <h1 style={{margin:"15px 25px 35px 25px"}}className = "ui header horizontal divider ">Trending</h1>
            <RenderCards background = "" products = {filterTrending()}/>
            <Contact />
        </div>
    </div>
    );
};

const mapStateToProps = (state) => {
    return {
        products: Object.values(state.productReducer)
    };
};

export default connect(mapStateToProps, {fetchProducts})(Home);