import React, {useState, useEffect} from 'react';
import _ from 'lodash';
import {Link} from 'react-router-dom';
import {CSSTransition} from 'react-transition-group';

import SearchProducts from './Search.js'
import Checkout from './Checkout'

  
const Sidebar = (props) =>{ 
    const [currentCart, setCart] = useState(false);
    const [inProp, setInProp] = useState(false);

    useEffect(() => {
        if (props.type){setInProp(true);}
        else{setInProp(false);}
    }, [props.type]);

    const calculateTotal = () =>{
        if (!props.cart || _.isEmpty(props.cart)) {return 0;}

        return (Object.values(props.cart).reduce((t, {id, quantity}) => t + quantity * parseFloat((props.products[id].sale || props.products[id].price).slice(1)), 0).toFixed(2));
    }

    const renderType = ()=>{
        if (props.type === "search" ){
            return <SearchProducts products={props.products}/>;
        }
        else if (props.type === "cart" ){
            return (
                <React.Fragment>
                    <div className = "ui divided items">
                        {renderCartItems()}
                    </div>
            <h3 style={{textAlign: "center"}}>Total: ${calculateTotal()}</h3>
                    <div onClick = {()=>setCart(true)} className = "ui fluid secondary button">
                        Checkout
                    </div>
                    {renderCheckout()}
                </React.Fragment>
            );
        }
    }
    let doublebug = false
    const renderCheckout = () =>{
        if (!currentCart){return null;}
        if (doublebug)return null;
        doublebug = true;
        return <Checkout total = {calculateTotal} clearCart = {props.clearCart} handleOff = {()=>setCart(false)} />;
    }

    const renderCartItems = () =>{
        if (!props.cart || _.isEmpty(props.cart)) {return <div style={{textAlign: "center"}} className = "item"><div className = "content"><div className = "header">No Items in Cart</div></div></div>;}
        
        return Object.keys(props.cart).map((key, index)=>{
            let item = props.cart[key];
            let product = props.products[item.id];
            return (
                <div key = {index} className = "item">
                    <div className = "ui tiny image">
                        <img src = {product.img}/>
                        <div style={{width: "100%", position: "absolute", bottom: "1em", display: "flex", justifyContent: "center"}}>
                        <h2 className = "cart__quantitycolor">{item.quantity}</h2>
                        </div>
                    </div>
                    <div className = "content">
                        <div className = "header">
                            <Link style={{color: "black"}} to = {`/shop/${product.id}`}>{product.header}</Link>
                        </div>
                        <div className = "meta">
                            <span style={product.sale ? {textDecoration: "line-through"} : {}} className="price">{product.price}</span>
                            <span style={{color: "red"}}className="sale">{product.sale}</span>
                        </div>
                        <div>
                            <p style={{color: "rgba(0,0,0,.4)"}} className = "limittext description">{product.description}</p>
                        </div>
                        <div style={{color: "black"}} className = "extra">
                            <span>Options: </span>
                            <span>{item.option1}</span>
                            <span>{item.option2}</span>
                            <span>{item.option3}</span>
                            <i onClick = {()=>{props.removeSelf(item.reducerid)}} style={{color: "black"}} className = "right floated large trash icon removecart"/>
                        </div>
                    </div>
                </div>
            );
        });
    }
 
    const renderSmallSidebar = () =>{
        if (!props.type)return null;
        return (
        <div className="sidebar sidebarsmall">
            <div style={{height: "99%", width: "100%", overflowY: "scroll"}} className = "ui segment">
                <div className = "ui horizontal divider">{props.type}</div>
                {renderType()}
            </div>
        </div>);

    }
    return (
        <div>
            <div className = "sidebarbig">
                <CSSTransition in={inProp} timeout={400} classNames = "slide">
                    <div className="sidebar">
                            <div style={{height: "99%", width: "100%", overflowY: "scroll"}} className = "ui segment">
                                <div className = "ui horizontal divider">{props.type}</div>
                                {renderType()}
                            </div>
                    </div>
                </CSSTransition>
            </div>
            {renderSmallSidebar()}
        </div>
    );
};

export default Sidebar;