import React, {useEffect, useState, useLayoutEffect} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

import {fetchProducts, addProductToCart, sidebarAction} from '../actions';
import OptionSelections from './OptionSelections';
import '../home.css';
import Contact from './Contact';
import RenderCards from './RenderCards';

  
const Item = (props) =>{ 

    function hideZoom() {
        let result = document.getElementById("myresult");
        let lens = document.getElementById("lens");
        lens.style.visibility = "hidden";
        result.style.visibility = "hidden";
    }

    function imageZoom(e,imgID, resultID) {
        var img, lens, result, cx, cy;
        img = document.getElementById(imgID);
        result = document.getElementById(resultID);
        /* Create lens: */
        lens = document.getElementById("lens");

        /* Calculate the ratio between result DIV and lens: */
        cx = result.offsetWidth / lens.offsetWidth;
        cy = result.offsetHeight / lens.offsetHeight;
        /* Set background properties for the result DIV */
        result.style.backgroundImage = "url('" + img.src + "')";
        result.style.backgroundSize = (img.width * cx) + "px " + (img.height * cy) + "px";
        /* Execute a function when someone moves the cursor over the image, or the lens: */
        moveLens(e)
        function moveLens(e) {
          var pos, x, y;
          /* Prevent any other actions that may occur when moving over the image */
          e.preventDefault();
          /* Get the cursor's x and y positions: */
          pos = getCursorPos(e);
          /* Calculate the position of the lens: */
          x = pos.x - (lens.offsetWidth / 2);
          y = pos.y - (lens.offsetHeight / 2);
          /* Prevent the lens from being positioned outside the image: */
          if (x > img.width - lens.offsetWidth) {x = img.width - lens.offsetWidth;}
          if (x < 0) {x = 0;}
          if (y > img.height - lens.offsetHeight) {y = img.height - lens.offsetHeight;}
          if (y < 0) {y = 0;}
          /* Set the position of the lens: */
          lens.style.left = x + "px";
          lens.style.top = y + "px";
          lens.style.visibility = "visible";
          /* Display what the lens "sees": */
          result.style.visibility = "visible";
          result.style.backgroundPosition = "-" + (x * cx) + "px -" + (y * cy) + "px";
        }
        function getCursorPos(e) {
          var a, x = 0, y = 0;
          e = e || window.event;
          /* Get the x and y positions of the image: */
          a = img.getBoundingClientRect();
          /* Calculate the cursor's x and y coordinates, relative to the image: */
          x = e.pageX - a.left;
          y = e.pageY - a.top;
          /* Consider any page scrolling: */
          x = x - window.pageXOffset;
          y = y - window.pageYOffset;
          return {x : x, y : y};
        }
      }

    useEffect(() => {
        props.fetchProducts();
        
    }, []);

    useLayoutEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    useEffect(() => {      
        if (props.products && props.products[props.match.params.id]){
            setImage(props.products[props.match.params.id].img);
            setQuantity(1);
            window.scrollTo(0, 0);
            setOption1(null);
            setOption2(null);
            setOption3(null);
            for (let i in props.products[props.match.params.id].options){
    
                if (i==0){
                    setOption1(props.products[props.match.params.id].options[i].values[0]);
                }
                else if (i==1){
                    setOption2(props.products[props.match.params.id].options[i].values[0]);
                }
                else if (i==2){
                    setOption3(props.products[props.match.params.id].options[i].values[0]);
                }
                
            }
        }
    }, [props.products, props.match.params.id]);

    const [currentQuantity, setQuantity] = useState(1);
    const [currentImage, setImage] = useState(null);

    const [currentOption1, setOption1] = useState(null); 
    const [currentOption2, setOption2] = useState(null);
    const [currentOption3, setOption3] = useState(null);

    const updateOptions = (optionnumber, value) =>{
        if (optionnumber === 0){
            setOption1(value);
        }
        else if (optionnumber === 1){
            setOption2(value);
        }
        else if (optionnumber === 2){
            setOption3(value);
        }
    }

    if (!props.products || !props.products[props.match.params.id]){return null;}
    let item = props.products[props.match.params.id];

    const renderCategoryCrumb = (categories) =>{
        return categories.join(" || ");
    }

    const renderOtherImages = () =>{
        if (!item.images || !item.images[0]){return null;}
        return item.images.map((image, index)=> <img style={currentImage === image ? {opacity: "1"} : {}} onClick = {()=>setImage(image)}key = {index} className = "other__images ui tiny image" src = {image}/>);
    }

    const handleChange = (e) =>{
        if (isNaN(e.target.value)){
            return;
        }
        else if (e.target.value < 0){
            return;
        }
        else if (e.target.value > 999){
            return;
        }
        else if (!e.target.value || e.target.value === '0' || e.target.value === 0){
            setQuantity('');
        }
        else if (parseInt(e.target.value) != e.target.value){
            return;
        }
        else{
            setQuantity(parseInt(e.target.value));
        }
    }

    const filterProducts = (products) =>{
        return products.filter((product) => product.categories.includes(item.categories[0]) && product !== item);
    }

    return (
        
        <div style={{display: "flex", flexDirection:"column", alignItems: "center"}}>
            
            <div style={{width: "100vw", padding: "20px", marginBottom: "10px", backgroundColor: "#eee", textAlign: "center"}} className = "ui breadcrumb">
                <Link style={{color: "grey"}} to="/" className = "section">Home</Link>
                <i style={{color: "grey"}} className="right angle icon divider"></i>
                <Link style={{color: "grey"}} to="/shop" className = "section">Shop</Link>
                <i style={{color: "grey"}} className="right angle icon divider"></i>
    <Link style={{color: "grey"}} to={{pathname: "/shop", state: item.categories[0]}} className = "section">{renderCategoryCrumb(item.categories)}</Link>
                <i style={{color: "black"}} className="right angle icon divider"></i>
                <div className = "active section">{item.header}</div>
            </div>
            <div className = "responsive__item">
                <div className = "responsive__otherimages">
                    <img onClick = {()=>setImage(item.img)} style={currentImage === item.img ? {opacity: "1"} : {}} className = "other__images ui tiny image mainimage" src = {item.img}/>
                    {renderOtherImages()}
                </div>
                
                <div onMouseLeave = {() => hideZoom()} onMouseMove = {(e) => imageZoom(e, "myimage", "myresult")} style={{maxWidth: "100vw", height: "100%"}} className = "ui centered large bordered image img-zoom-container">
                    <div id = "lens" className = "img-zoom-lens"></div>
                    <img  id="myimage"  src = {currentImage} />
                    
                </div>
                <div className = "responsive__item__description" style={{display: "flex", flexDirection: "column"}}>
                    <div style={{margin: "10px 0 5px 0"}} className = "ui header">{item.header}</div>
                    <div id="myresult" className="img-zoom-result"></div>
                    <div style={{display: "flex", fontSize: "1.5em", marginBottom: "0.75em", marginTop: "0.1em"}} >
                        <div style={item.sale ? {textDecoration: "line-through", marginRight: "7px"} : {}}> {item.price}</div>
                        <div style={{color: "red"}}>{item.sale}</div>
                    </div>

                    <div style={{marginBottom: "0.75em", color: "#444", fontSize: "1.15em"}}>{item.description}</div>

                    <OptionSelections updateOptions = {updateOptions} item = {item} justify = "flex-start"/>
                    <div style={{display: "flex"}}>
                    <div style={{width: "10em", textAlign: "center"}} className = "ui input">
                        <div style={{height: "100%", position: "absolute", left: "1em", display: "flex", alignItems: "center"}}>
                            <i onClick = {() => !(currentQuantity-1) || (currentQuantity - 1 < 0) || setQuantity(currentQuantity - 1)} className = "quantity__clickable minus large icon"/> 
                        </div>
                        <div style={{height: "100%", position: "absolute", right: "1em", display: "flex", alignItems: "center"}}>
                            <i onClick = {
                                () => {
                                    if (currentQuantity === ''){ setQuantity(1)}
                                    else{(currentQuantity + 1 > 999) || setQuantity(currentQuantity + 1)}
                                }
                                
                                } className = "quantity__clickable plus large icon"/>
                        </div>
                        <input onChange={handleChange} type="text" style={{margin: "5px 0 5px 0px", borderRadius: "2.5em", textAlign: "center", fontWeight: "bold", fontSize: "1.25em"}} value = {currentQuantity}/>
                        
                    </div>

                    <div style={{marginTop: "10px", marginLeft: "10%", width: "calc(90% - 10em)"}}>
                        <div onClick = {()=>{props.addProductToCart(item, currentOption1, currentOption2, currentOption3, (currentQuantity || 1)); props.sidebarAction("cart")}} className = "ui fluid black button">Add To Cart</div>
                    </div>
                    </div>
                </div>
       
            </div>
            <div style={{width: "100%", marginTop: "50px", backgroundColor: "#ebeff5", display: "flex", flexDirection: "column"}}>
                <div style={{alignSelf: "center", marginTop: "30px", marginBottom: "30px", border: "2px solid black", padding: "0.8em 1.6em", borderRadius: "1.6em", color: "black", fontWeight: "bold", fontSize: "1.15em"}}>
                                Details
                </div>
                <div style={{marginLeft: "10%", marginRight: "10%", marginBottom: "60px", fontSize: "1.1em"}}>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Lorem:</span><span style={{}}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus lobortis nec sem sit amet posuere. Donec interdum lacus eget blandit.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Ipsum:</span><span style={{}}>Etiam sed aliquam nulla. Pellentesque facilisis sollicitudin quam, ut vulputate felis rhoncus et. In sed vestibulum orci.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Ipsum:</span><span style={{}}>Sed vulputate scelerisque lorem a luctus. Donec luctus dolor nibh.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Lorem:</span><span style={{}}>Sed est elit, bibendum sed magna nec, vehicula elementum ipsum. Vestibulum ante ipsum primis in.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Ipsum:</span><span style={{}}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed eget lacinia ipsum. Sed euismod quam.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Lorem:</span><span style={{}}>Quisque purus enim, imperdiet eleifend bibendum quis, tincidunt ac dui. Nullam maximus diam enim, et eleifend nisi molestie sed. Fusce in.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Ipsum:</span><span style={{}}>Etiam sed aliquam nulla. Pellentesque facilisis sollicitudin quam, ut vulputate felis rhoncus et. In sed vestibulum orci.</span></div>
                    <div style={{marginBottom: "5px"}}><span style={{fontWeight: "bold", color: "black",  marginRight: "5px"}}>Lorem:</span><span style={{}}>Mauris vestibulum ipsum sit amet felis molestie sodales. Proin facilisis nisl magna, sit amet suscipit.</span></div>

                </div>
            </div>
            <div style={{width: "100%", marginTop: "30px", textAlign: "center"}}>
                <h1 style={{marginBottom: "30px"}}>Similar Products</h1>         
                <RenderCards products = {filterProducts(Object.values(props.products)).slice(0,4)}/>
            </div>
            <Contact />
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        products: state.productReducer
    };
};

export default connect(mapStateToProps, {fetchProducts, addProductToCart, sidebarAction})(Item);