import axios from 'axios';

const api = axios.create({baseURL: "http://localhost:3001"});

export const sidebarAction = (type) =>{
    return {
        type: "SIDEBAR",
        payload: type
    };
};

export const clearCartAction = () =>{
    return {
        type: "CLEAR_CART"
    };
};

export const addProductToCart = (item, option1, option2, option3, quantity) =>{
    let reducerid = Math.floor(Math.random() * (100000000000 - 0 + 1) + 0);
    return {
        type: "ADD_TO_CART",
        payload: {id: item.id, option1: option1, option2: option2, option3: option3, quantity: quantity, reducerid: reducerid}
    };
};

export const removeAction = (reducerid) =>{
    return {
        type: "REMOVE_CART",
        payload: reducerid
    };
};

export const fetchProducts = () => async dispatch =>{
    const response = await api.get('/products');
    
    dispatch( {
        type: "FETCH_PRODUCTS",
        payload: response.data
    });
};


