import _ from 'lodash';


const cartReducer = (state = {}, action) => {
    switch (action.type){
        case "ADD_TO_CART":
            return {...state, [action.payload.reducerid]: action.payload};
        case "REMOVE_CART":
            return _.omit(state, action.payload);
        case "CLEAR_CART":
            return {};
        default:
            return state;
    }
};

export default cartReducer;