import React, {useEffect, useState} from 'react';
import ReactDOM from 'react-dom';
import { CSSTransition } from 'react-transition-group';

import '../reactanimations.css'


const Modal = props =>{
    const [currentInProp, setInProp] = useState(false);
    useEffect(() => {
        setInProp(true);
    }, []);

    return ReactDOM.createPortal(
        <CSSTransition in={currentInProp} timeout={300} classNames = "modalslide"> 
            <div style = {{position: "fixed", top: "0%", left: "50%", height: "150vh", width: "100vw", opacity: "0", marginTop: "-50vh", paddingBottom: "50vh"}} onClick = {props.onDismiss} className = "ui dimmer modals visible active">
                <div onClick={(e) => e.stopPropagation()} className = "ui tiny modal visible active">
                    <i onClick = {props.onDismiss} className = "close icon"></i>
                    <div style={{textAlign: "center"}}className = "header">
                        {props.header}
                    </div>
                    <div style={{textAlign: "center"}} className = "image content">
                        {props.content}
                    </div>
                    <div className = "actions">
                        {props.actions}
                    </div>
                </div>
            </div>
        </CSSTransition>,
        document.querySelector('#modal')
    );
};

export default Modal;