import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';


import {sidebarAction, removeAction, clearCartAction} from '../actions';
import Sidebar from './Sidebar';

import '../nav.css';
  
const Nav = (props) =>{ 

    const handleSidebarChange = (type)=>{
        if (props.sidebarCurrent === type){
            props.sidebarAction(null);
        }
        else {
            props.sidebarAction(type);
        }
    }

    const renderSidebar = () =>{
       
            return <Sidebar clearCart = {props.clearCartAction} removeSelf = {props.removeAction} cart = {props.cart} products = {props.products} type={props.sidebarCurrent}/>;
        
    }

    return (
        
    <div className="responsive__sticky">
        <div className = 'ui secondary pointing huge stackable menu'>
            
            <Link to= {"/"} className = "item" >LogoLOGOlogO</Link>

                
            <div className="right menu ">
                <Link to= {"/"} className = {`item clickable ${props.location.pathname === "/" || props.location.pathname.slice(0,10) === "/quickshop" ? "active" : ""}`}>Home</Link>
                <Link to= {"/shop"} className = {` item clickable ${props.location.pathname.slice(0,5) === "/shop" ? "active" : ""}`}>Shop</Link>
            </div>
               

            
            <div className = "right menu">   
                <div onClick= {()=>{handleSidebarChange("search")}} className = {`clickable ui item ${props.sidebarCurrent === "search" ? "active" : ""}`}><i className = "search icon"/></div>       
                <div onClick= {()=>{handleSidebarChange("cart")}} className = {`clickable ui item ${props.sidebarCurrent === "cart" ? "active" : ""}`}><i className =  "shopping cart icon"/></div>
            </div>
           
        </div>
        <div>
            {renderSidebar()}
        </div>
    </div>
    );
};

const mapStateToProps = (state) => {
    return {
        sidebarCurrent: state.sidebarReducer.sidebar,
        products: state.productReducer,
        cart: state.cartReducer
    };
};

export default connect(mapStateToProps, {sidebarAction, removeAction, clearCartAction})(Nav);