import React from 'react';
import {connect} from 'react-redux';

import Modal from './Modal';
import history from '../history';

class Checkout extends React.Component {
    constructor(props){
        super(props);
        this.state = {error: false, value: ''};
    }
    
     renderActions(){
         
         return(
        <React.Fragment> 
            <button onClick = {()=>{
                if (this.state.value === '' &&  this.props.total() !==0){this.setState({error: true})}
                else{
                    this.props.handleOff(); 
                    this.props.clearCart();
                }
                }} style={{marginLeft: "auto", marginRight: "auto", marginBottom: "1px"}} className={`ui fluid ${this.props.total() === 0 ? "secondary" : "negative"} button`}>{this.renderCheckoutText()}</button>
            <button onClick = {()=>this.props.handleOff()}  style={this.props.total() === 0 ? {display: "none"} : {marginLeft: "auto", marginRight: "auto"}} className="ui fluid secondary button">Cancel</button>
        </React.Fragment>
         );
     };

     renderCheckoutText = () =>{
        if (this.props.total() === 0){
            return "Go Back";
        } 
        else {
            return "Checkout";
        }
     }


     handleChange = (e) =>{
        this.setState({
            value: e.target.value
        });
    }

     renderContent(){
        if (this.props.total() === 0){
            return (
            <div className = "ui header" style={{marginLeft: "auto", marginRight: "auto"}}>
                No Items
            </div>);
        }
    
        else{
            return (
            <div style={{width: "100%"}}>
                <div style={{display: "flex", justifyContent: "center", fontSize: "1.1em", marginBottom: "0.5em"}}>Note: No actual products will be shipped or purchased</div>
                <div className="ui form">
                    <div className={`field ${this.state.error ? "error" : ""}`}>
                        <div className="ui fluid input">
                            <input value = {this.state.value} onChange = {this.handleChange} style={{textAlign: "center"}} type="text" placeholder="Enter Address"/>
                        </div>
                    </div>                    
                </div>
                <div className = "ui header" style={{marginLeft: "auto", marginRight: "auto"}}>
                Total: ${this.props.total()}
                </div>
            </div>);
        }
     };

    render(){
        return (
            <div>
                <Modal header="Cart" content={this.renderContent()} onDismiss={() => this.props.handleOff()} actions = {this.renderActions()}/>
            </div>
    );
    }

};

const mapStateToProps = (state) =>{
    return {cart: state.cartReducer}
};

export default connect(mapStateToProps)(Checkout);