import React, {useState, useEffect} from 'react';
  
const OptionSelection = (props) =>{ 
    let item = props.item;

    const [currentOption1, setOption1] = useState(!item.options || !item.options[0] || item.options[0].values[0]);
    const [currentOption2, setOption2] = useState(!item.options || !item.options[1] || item.options[1].values[0]); //max amount of options is limited to three, but can all be conditionaly rendered depending on existence within api
    const [currentOption3, setOption3] = useState(!item.options || !item.options[2] || item.options[2].values[0]);

    useEffect(() => {      

            setOption1(!item.options || !item.options[0] || item.options[0].values[0]);
            setOption2(!item.options || !item.options[1] || item.options[1].values[0]);
            setOption3(!item.options || !item.options[2] || item.options[2].values[0]);
        
    }, [item.options]);

    const handleOptionClick = (value, optionnumber) =>{
        props.updateOptions(optionnumber, value);
        if (optionnumber === 0){
            setOption1(value);
        }
        else if (optionnumber === 1){
            setOption2(value);
        }
        else if (optionnumber === 2){
            setOption3(value);
        }
    }

    const renderOptionValues = (values, optionnumber) =>{
        const optionStyle={border: "2px solid black", borderRadius: "16px", textAlign: "center", padding: "0 1em", marginRight: "0.25em", marginTop: "0.25em"};

        return values.map((value, index)=>{
            return <div onClick = {()=> handleOptionClick(value, optionnumber)} className = {currentOption1 === value || currentOption2 === value || currentOption3 === value? "option__value__enabled" : "option__value"} key = {index} style={optionStyle}>{value}</div>;
        });
    }

    const renderOptions = () =>{
        if (!item.options || !item.options[0]){return <h5 style={{marginBottom: "0.25em"}}>No Options for this Product</h5>;}
        return item.options.map((option, index)=>{return (
            <div key={index}>
                <h5 style={{marginBottom: "0.25em"}}>{option.title}:</h5>
                <div style={{marginBottom: "1em", display: "flex", flexWrap: "wrap", justifyContent: props.justify}}>
                    {renderOptionValues(option.values, index)}
                </div>
            </div>);
        }
        );
    }

    return (
    <React.Fragment>
        {renderOptions()}
    </React.Fragment>
    );
};

export default OptionSelection;