import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import Modal from './Modal';

import history from '../history';
import {addProductToCart, sidebarAction} from '../actions';
import OptionSelections from './OptionSelections';


const QuickShop = (props) => {
    const [currentQuantity, setQuantity] = useState(1);
    const [currentImage, setImage] = useState(props.products[props.id].img);

    useEffect(() => {
            for (let i in props.products[props.id].options){
                if (i==0){
                    setOption1(props.products[props.id].options[i].values[0]);
                }
                else if (i==1){
                    setOption2(props.products[props.id].options[i].values[0]);
                }
                else if (i==2){
                    setOption3(props.products[props.id].options[i].values[0]);
                }              
            }
    }, []);

    const [currentOption1, setOption1] = useState(null); 
    const [currentOption2, setOption2] = useState(null);
    const [currentOption3, setOption3] = useState(null);

    const updateOptions = (optionnumber, value) =>{
        if (optionnumber === 0){
            setOption1(value);
        }
        else if (optionnumber === 1){
            setOption2(value);
        }
        else if (optionnumber === 2){
            setOption3(value);
        }
    }

     const renderActions = () => {
         
        return(
            <React.Fragment> 
                <button onClick = {()=>{props.addProductToCart(props.products[props.id], currentOption1, currentOption2, currentOption3, (currentQuantity || 1)); props.sidebarAction("cart"); props.removeQuickShop()}} className="ui primary button">Add to Cart</button>
                <button onClick= {()=>props.removeQuickShop()} className="ui button">Cancel</button>
            </React.Fragment>
             );
         
     }

     
     if (props.products[props.id].images){var totalimages = [props.products[props.id].img, ...props.products[props.id].images]}
     else{totalimages = [props.products[props.id].img]}

     const handleCycle = ()=>{
        for (let i = 0; i < totalimages.length; i++){
            if (currentImage === totalimages[i]){
                setImage(totalimages[((i+1) % (totalimages.length))]);
                break;
            }
        }
     }

     const handleChange = (e) =>{
        if (isNaN(e.target.value)){
            return;
        }
        else if (e.target.value < 0){
            return;
        }
        else if (e.target.value > 999){
            return;
        }
        else if (!e.target.value || e.target.value === '0' || e.target.value === 0){
            setQuantity('');
        }
        else if (parseInt(e.target.value) != e.target.value){
            return;
        }
        else{
            setQuantity(parseInt(e.target.value));
        }
    }

     const renderContent = () => {
        return (
        <React.Fragment>
            <div  className="ui medium image">           
                <img src={currentImage}/>
                <div onClick ={()=>handleCycle()}style={{borderRadius: "0", paddingTop: "0.5em", paddingBottom: "0.5em"}} className = "ui secondary fluid bottom attached button">Next Image</div>
            </div>
            <div className="description">
                <div className="ui header">{props.products[props.id].header}</div>
                <div style={{display: "flex", justifyContent: "center", fontSize: "1.25em", marginTop: "-0.75em", marginBottom: "0.5em"}}>
                     <div style={props.products[props.id].sale ? {textDecoration: "line-through", marginRight: "7px"} : {}}> {props.products[props.id].price}</div>
                      <div style={{color: "red"}}>{props.products[props.id].sale}</div>
                  </div>
                  <div style={{display: "flex", flexDirection: "column", alignItems: "center"}}>
                <OptionSelections updateOptions = {updateOptions} item = {props.products[props.id]} justify = "center"/>
                </div>
                <div style={{width: "10em", textAlign: "center"}} className = "ui input">
                        <div style={{height: "100%", position: "absolute", left: "1em", display: "flex", alignItems: "center"}}>
                            <i onClick = {() => !(currentQuantity-1) || (currentQuantity - 1 < 0) || setQuantity(currentQuantity - 1)} className = "quantity__clickable minus large icon"/> 
                        </div>
                        <div style={{height: "100%", position: "absolute", right: "1em", display: "flex", alignItems: "center"}}>
                            <i onClick = {
                                () => {
                                    if (currentQuantity === ''){ setQuantity(1)}
                                    else{(currentQuantity + 1 > 999) || setQuantity(currentQuantity + 1)}
                                }
                                
                                } className = "quantity__clickable plus large icon"/>
                        </div>
                        <input onChange={handleChange} type="text" style={{margin: "5px 0 5px 0px", borderRadius: "2.5em", textAlign: "center", fontWeight: "bold", fontSize: "1.25em"}} value = {currentQuantity}/>
                        
                    </div>
            </div>
        </React.Fragment>);
     }

    return (
        <div>
            <Modal header="QuickShop" content={renderContent()} onDismiss={() => props.removeQuickShop()} actions = {renderActions()}/>
        </div>
    );
}


const mapStateToProps = (state) => {
    return {
        products: state.productReducer
    };
};

export default connect(mapStateToProps, {addProductToCart, sidebarAction})(QuickShop);